<template>
  <div id="app">
    <lemonheader msg="this is message" v-on:contents-status="contentsview"/>
    <contents 
        v-bind:val="contentsstatus" 
        ref="contentsrender"
    ></contents>
    <lemonfooter />
  </div>
</template>

<script>

import contents from './components/contents.vue'
import lemonheader from './views/header.vue'
import lemonfooter from './views/footer.vue'

export default {
  name: 'App',
  data(){
      return{
        contentsstatus:'test',
        isContents: 'none'
      }
  },
  methods:{
      contentsview:function(fromchild){
          this.contentsstatus = fromchild
          this.$refs.contentsrender.contetnsrender(fromchild)
          this.$refs.contentsrender.isContents = fromchild
      }

  },

  components: {
      contents,
      lemonheader,
      lemonfooter
  }
}

</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>